.checkout__main {
	display: flex;
	flex-direction: row;
}
.order__description {
	flex: 0.5;
}

.order__container__checkout {
	padding-left: 60px;
}

.order__payment__container {
	right: 0;
	top: 0;
	width: 38%;
	position: fixed;
	background-color: #f6f6f6;
	height: 100vh;
}

.order__payment {
	padding: 120px 80px;
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
}
.place__order__container {
	background-color: #03703c;
	padding: 20px;
	color: white;
	display: flex;
	justify-content: center;
	align-items: center;
	font-size: 20px;
	font-weight: 500;
	cursor: pointer;
	width: 100%;
	font-family: sans-serif;
}

.payment__item {
	margin-bottom: 20px !important;
	width: 100%;
}

.delivery__guideline {
	font-size: 13px;
	line-height: 20px;
	font-weight: normal;
	font-family: sans-serif;
	color: rgb(117, 117, 117);
}

.divider {
	color: black;
}

.bill__item {
	display: flex;
	justify-content: space-between;
	width: 100%;
	font-size: 16px;
	line-height: 24px;
	font-weight: normal;
	font-family: sans-serif;
}
.total__container {
	display: flex;
	justify-content: space-between;
	font-size: 20px;
	line-height: 28px;
	font-weight: 500;
	font-family: sans-serif;
}

.order__items__container {
	display: flex;
	flex-direction: column;
}

.order__item {
	display: flex;
	flex-direction: column;
	padding: 10px 0px;
}

.order__item__quantity {
	flex: 0.2;
	align-items: center;
	justify-content: center;
}

.item__right__container {
	flex: 0.8;
	display: flex;
	justify-content: space-between;
}

.item__quantity__dropdown {
	margin: 0;
}

.order__item__container {
	display: flex;
	align-items: center;
	margin-bottom: 10px;
}

.order__heading {
	font-size: 20px;
	font-weight: 400;
	font-family: UberMoveText, sans-serif;
}

.delivery__container__main {
	display: flex;
	flex-direction: column;
	padding-left: 60px;
}

.delivery__item {
	display: flex;
	align-items: center;
	padding: 10px 0px;
}

.delivery__icon {
	flex: 0.05;
}

.delivery__container {
	flex: 0.95;
	display: flex;
	flex-direction: row;
	justify-content: space-between;
	align-items: center;
}

.delivery__edit__container {
	background-color: #eeeeee;
	padding: 14px;
	border-radius: 50px;
	cursor: pointer;
}

.location__box__main {
	position: absolute;
	top: 50%;
	left: 50%;
	background-color: white;
	transform: translate(-50%, -50%);
	z-index: 10;
	width: 450px;
	overflow: auto;
	/* height: 100px; */
	border: 2px solid #000;
	box-shadow: 24px;
	padding: 32px;
	display: flex;
	flex-direction: column;
	justify-content: space-between;
}

.add_item__container {
	display: flex;
	justify-content: space-between;
	width: 100%;
}

.address_item {
	cursor: pointer;
}

.order__instruction{
	margin-top: 20px;
}

.order__instruction > div {
	width: 100%;
}

.payment__order__instructions__container{
	display: flex;
	flex-direction: column;
	width: 100%;
}

.instructions__heading{
	font-size: 20px;
	font-weight: 600;
	font-family: sans-serif;
	padding-bottom: 10px;
}

.payment__order__instructions{
	font-weight: 100;
	font-family: sans-serif;
	color: rgb(107, 107, 107);
	padding-bottom: 10px;
}